<section className="faq-section">
<img src={LineBg} className="bg-faq" />
<Container>
<div className="main-page-heading">
<p>Your questions, our answers</p>  
<h2>FAQ</h2>
</div>

<div className="faq-content">
<Accordion flush>
  <Accordion.Item eventKey="0">
    <Accordion.Header>What is Bulk?</Accordion.Header>
    <Accordion.Body>
      We position ourselves as a decentralized venture capital platform.<br/>
      We incubate projects, finance, support them and provide our 
          community the opportunity to invest in these very projects at an
          early stage along with us.
    </Accordion.Body>
  </Accordion.Item>
  <Accordion.Item eventKey="1">
    <Accordion.Header>Where can I get acquainted with the projects that youve supported and invested in?</Accordion.Header>
    <Accordion.Body>
    The All Deals tab on the site and the Public Area of our app display some projects that we and our community have invested in at an early stage (seed / presale / private phases).
    <br/>
    However, due to confidentiality reasons, the vast majority of our deals are not posted in the public All Deals area. These deals are only accessible to our members in the Member Area.

    </Accordion.Body>
  </Accordion.Item>
  <Accordion.Item eventKey="2">
    <Accordion.Header>How can I access the Members Area?</Accordion.Header>
    <Accordion.Body>
      Our Members Area has a tiered system. Higher Tiers grant larger individual allocations and provide access to more exclusive deals that might not be accessible in lower Tiers.
      <br/>
      To access Members Area you need to lock the following number of tokens on our Lock Platform bulklock.web.app:<br/>
      Top 10 - 10 users with highest locked balances<br/>
      Tier 1 - 1 000 000 BULK<br/>
      Tier 2 - 500 000 BULK<br/>
      Tier 3 - 100 000 BULK<br/>
      Tier 4 - 10 000 BULK<br/>
      You can find the user manual <a href="https://crypto-bulk-intl.medium.com/how-to-join-bulk-community-7759b722ac21">here</a>.

    </Accordion.Body>
  </Accordion.Item>
  <Accordion.Item eventKey="3">
    <Accordion.Header>Is there a guaranteed allocation for the Tier 4 members?</Accordion.Header>
    <Accordion.Body>
      Depending on a deal, Tier 4 can have either a guaranteed allocation or a limited number of allocations that can be raffled among participants. Lower tiers are sometimes excluded from the deals higher tiers take part in.
    </Accordion.Body>
  </Accordion.Item>
  <Accordion.Item eventKey="4">
    <Accordion.Header>How often are new deals posted? </Accordion.Header>
    <Accordion.Body>
      All the important information concerning the past and new deals is posted in our Deal Announcements Channel in <a href="https://t.me/BulkDealsAnn">Telegram</a>. You can track new deal postings for each Tier on this channel.
    </Accordion.Body>
  </Accordion.Item>
</Accordion>
</div>
</Container>
</section>


